.header {
    max-width: 880px;
    min-height: 74px;
    margin: 0 auto;
    border-bottom: 1px solid rgba(84, 84, 84, .7);
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 648px) {
    .header {
        width: 100%;
        min-height: 56px;
    }
}