.auth__title {
    display: block;
    margin: 20px 0 0;
    padding: 0;
    text-align: center;
    color: #fff;
    font-family: "Inter", Arial, sans-serif;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
}

@media screen and (max-width: 648px) {
    .auth__title {
        font-size: 20px;
    }
}